* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #e4f8f6;
  margin: 90px 0 0;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  max-width: 320px;
  margin-bottom: 16px;
  padding: 8px;
}

ul, ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

.search-form {
  width: 100%;
  text-align: center;
  background-color: #40e0d0;
  padding: 20px 0;
  position: fixed;
  top: 0;
}

input {
  border: none;
  border-radius: 4px;
  margin-right: 10px;
  padding: 8px 0 8px 10px;
  box-shadow: 0 1px 6px rgba(46, 47, 66, .08), 0 1px 1px rgba(46, 47, 66, .16), 0 2px 1px rgba(46, 47, 66, .08);
}

.submit-button {
  background-color: #e3ecec;
  border: none;
  border-radius: 4px;
  padding: 8px 32px;
  transition-property: background-color;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  box-shadow: 0 1px 6px rgba(46, 47, 66, .08), 0 1px 1px rgba(46, 47, 66, .16), 0 2px 1px rgba(46, 47, 66, .08);
}

.submit-button:hover, .submit-button:focus {
  cursor: pointer;
  background-color: #dadfdf;
}

.gallery {
  flex-wrap: wrap;
  justify-content: space-around;
  display: flex;
}

.photo-card {
  border-radius: 0 0 8px 8px;
  margin-bottom: 20px;
  box-shadow: 0 1px 6px rgba(46, 47, 66, .08), 0 1px 1px rgba(46, 47, 66, .16), 0 2px 1px rgba(46, 47, 66, .08);
}

.one-photo {
  border-radius: 4px 4px 0 0;
  display: block;
}

.info {
  width: 300px;
  text-align: center;
  background-color: #fff;
  border-radius: 0 0 8px 8px;
  display: flex;
}

.load-more {
  color: #fafafa;
  background-color: #2dd8c7;
  border: none;
  border-radius: 4px;
  margin-bottom: 20px;
  margin-left: auto;
  margin-right: auto;
  padding: 8px 32px;
  transition-property: background-color;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  display: flex;
  box-shadow: 0 1px 6px rgba(46, 47, 66, .08), 0 1px 1px rgba(46, 47, 66, .16), 0 2px 1px rgba(46, 47, 66, .08);
